@import '_colors';
@import '_mixins';

body {

  section.main {
    background: url('../img/token_bg.jpeg');
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    color: $white;
    font-weight: 500;
    .message_line {
      background: rgba(0,0,0,.3);
      display: inline-block;
      width: calc(100% + 30px);
      margin-top: 30vh;
      text-align: center;
      padding: 50px 0;
      .message {
        background: $lightblue;
        display: inline-block;
        width: 275px;
        border-radius: 5px;
        position: relative;
        span {
          display: block;
          text-transform: uppercase;
          font-size: 1em;
          letter-spacing: 0.07em;
          padding: 20px 0 0; }
        &::after {
          content: '';
          display: inline-block;
          width: 15px;
          height: 15px;
          background: $lightblue;
          position: relative;
          top: 10px;
          transform: rotate(45deg); } } }
    .message_subline {
      h2 {
        text-align: center;
        margin: 30px 0 10px;
        a {
          color: $white;
          cursor: pointer;
          transition: color .2s;
          &:hover, &:visited, &:active {
            color: rgba($white, .8); } } }
      i {
        margin: 0 5px; } } }

  section.services {
    background: $darkblue;
    color: $white;
    font-weight: 300;
    padding-bottom: 60px;
    h1 {
      text-align: center;
      font-size: 1.8em;
      line-height: 1.2em;
      letter-spacing: 0.05em;
      margin: 40px 0; }
    > div {
      text-align: center;
      img {
        width: 40%;
        max-width: 173px;
        margin-bottom: 30px; }
      span {
        display: block;
        text-align: center;
        font-size: .95em;
        letter-spacing: 0.05em;
        line-height: 1.2em;
        &:last-of-type {
          margin-bottom: 40px; }
        strong {
          color: $orange; } } }
    .extra {
      color: rgba($white, .7);
      font-size: .8em;
      line-height: 2em;
      &.detailed {
        margin-top: 40px;
        @include extra-text(rgba(255,255,255,.75), $darkblue); } } }

  section.bio {
    @include section-border($bggrey);
    @include section-regular($orange, $orange);
    background: url('../img/support_bg.png');
    background-size: cover;
    .row {
      .photo {
        text-align: center;
        img {
          width: 100vw;
          max-width: 400px; } } } }

  section.examples {
    @include section-regular($lightblue, $lightblue);
    background-size: cover;
    background: url('../img/support_bg.png');
    color: darken($brown, 15%);
    max-width: 100vw;
    overflow-x: hidden;
    .row {
      h2 {
        margin-bottom: 50px; }
      ul {
        padding: 0 30px; }
      .docs-heading {
        @include rolldown-heading; }
      .docs-content {
        @include rolldown-content; }
      &.else {
        h3 {
          margin-top: 20px;
          text-align: center; } }
      &.extra {
        @include extra-text(rgba($lightblue, .3), inherit);
        text-align: center;
        font-weight: 300; } } }

  section.q-a {
    @include section-regular($orange, $orange);
    @include section-border(lighten($orange, 31%));
    padding-bottom: 50px;
    color: $brown;
    .row {
      padding-bottom: 0;
      h2 {
        margin-top: 0; } }
    .qa-heading {
      @include rolldown-heading;
      font-size: 1.3em; }
    .qa-content {
      padding-top: 0;
      @include rolldown-content;
      p {
        margin: 10px 20px 0;
        font-weight: 300; } }
    @media all and (min-width: 768px) {
      > .row {
        padding-bottom: 0; } } }

  section.customers {
    @include section-regular($orange, $orange);
    background: url('../img/support_bg.png');
    background-size: cover;
    color: darken($brown, 15%);
    max-width: 100vw;
    overflow-x: hidden;
    p {
      text-align: center;
      font-size: .95em;
      line-height: 1.4em;
      font-weight: 300;
      margin-top: 20px;
      margin-bottom: 35px; } }

  @media all and (min-width: 375px) {
    section.bio {
      .row {
        h2 {
          .wrap {
            display: none; } } } } }

  @media all and (min-width: 768px) {
    section.main {
      background-size: cover;
      background-position: center;
      .message_line {
        margin-top: 28vh;
        text-align: center;
        padding: 60px 0 35px;
        .message {
          width: 600px;
          border-radius: 5px;
          span {
            font-size: 1.6em;
            letter-spacing: 0.09em;
            padding: 30px 0 10px;
            &::after {
              top: 15px; } } } } }

    section.services {
      padding: 40px 60px 60px;
      h1 {
        font-size: 2.6em;
        letter-spacing: 0.1em;
        margin: 25px 0 50px;
        br {
          display: none; } }
      .row {
        > div {
          span {
            font-size: 1em;
            strong {
              font-size: 1.2em; } } }
        &.detailed {
          border-radius: 20px; } } }

    section.bio {
      .row {
        &:nth-child(odd) {
          > div {
            float: right; } }
        .photo {
          text-align: center;
          img {
            top: -30px;
            width: 100%;
            max-width: 400px;
            min-width: 0; } }
        h2 {
          margin: 70px 0; }
        ul {
          font-size: 1.2em; } } }

    section.examples {
      .row.else {
        h3 {
          text-align: left; } } } } }
